import { CLDR, RelativeTimeFieldType} from "@phensley/cldr";

const DOW: RelativeTimeFieldType[] = ['sun', 'mon', 'tue', 'wed', 'thu', 'fri', 'sat'];

export default (dateTimeStamp: number, refDateTimeStamp: number, cldr: CLDR) => {
  
  const cldrDate = cldr.Calendars.toGregorianDate({ date: dateTimeStamp});
  const cldrRefDate = cldr.Calendars.toGregorianDate({ date: refDateTimeStamp});
  // Default calendar date to use if we don't have any custom formatting
  const defaultCalendarDate = cldr.Calendars.formatDate({ 
    date: dateTimeStamp,
    zoneId: "America/New_York"
  }, { 
    context: 'standalone', 
    skeleton: 'yMMMd'
  });
  // Relative date logic
  const relativeContext = 'begin-sentence';
  const before = cldrDate.compare(cldrRefDate) < 0;
  let [field, amt] = cldrDate.relativeTime(cldrRefDate);
  const dow = DOW[cldrDate.dayOfWeek() - 1];
  if (field === 'week' && amt < 2) {
    // Normalize week to day
    field = 'day';
    amt *= 7;
  }
  if (before) {
    // Negate the amount because CalendarDate.relativeTime returns the absolute value
    amt *= -1;
  }
  if (field === 'day') {
    if (Math.abs(amt) < 2) {
      // Yesterday, Today, Tomorrow
      const relativeTime = cldr.Calendars.formatRelativeTimeField(amt, 'day', { context: relativeContext });
      let formattedDate = cldr.Calendars.formatDate({ 
        date: dateTimeStamp,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        time: 'short'
      });
      return `${relativeTime} ${formattedDate}`;
    }
    if (amt > 1 && amt < 7) {
      // This Monday
      return cldr.Calendars.formatDate({ 
        date: dateTimeStamp,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        skeleton: 'EEEEjm'
      });
    }
    if (amt >= 7 && amt < 14) {
      // Next Monday
      return defaultCalendarDate;
    }
    if (amt > -7 && amt < -1) {
      // Last Monday
      let relativeTime = cldr.Calendars.formatRelativeTimeField(-1, dow, { context: relativeContext });
      let formattedDate = cldr.Calendars.formatDate({ 
        date: dateTimeStamp,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        time: 'short'
      });
      return `${relativeTime} ${formattedDate}`;
    }
  }
  if (field === 'hour' || field === 'minute' || field === 'second' || field === 'millis') {
    // We don't worry about these relative time notations, we just want to display "Today 1:56PM"
    const relativeTime = cldr.Calendars.formatRelativeTimeField(0, 'day', { context: relativeContext });
    let formattedDate = cldr.Calendars.formatDate({ 
      date: dateTimeStamp,
      zoneId: "America/New_York"
    }, { 
      context: 'standalone', 
      time: 'short'
    });
    return `${relativeTime} ${formattedDate}`;
  }
  // Continue adding conditions..

  // Finally, format the date if no conditions matched
  return defaultCalendarDate;
};