import * as React from 'react';
import classnames from 'classnames';

export interface IHumanizeDataItem {
  startDate: string;
  endDate: string;
  momentOutput: string;
  cldrOutput: string;
}

const HumanizedTable: React.FC<{ tableData: IHumanizeDataItem[] }> = ({ tableData }) => (
    <div className="tableContainer">
        <table>
          <tbody>
            <tr>
              <th>Match</th>
              <th>Start Date</th>
              <th>End Date</th>
              <th>Moment Output</th>
              <th>CLDR Output</th>
            </tr>
            {
              tableData.map((rowItemData, i) => {
                const {
                  startDate,
                  endDate,
                  momentOutput,
                  cldrOutput
                } = rowItemData;
                const doValuesMatch = momentOutput === cldrOutput;
                return (
                  <tr key={i} className={classnames({mismatch: !doValuesMatch})}>
                    <td>{doValuesMatch ? <span>&#9989;</span> :  <span>&#10060;</span>}</td>
                    <td>{startDate}</td>
                    <td>{endDate}</td>
                    <td>{momentOutput}</td>
                    <td>{cldrOutput}</td>
                  </tr>
                );
              })
          }
        </tbody>
        </table>
      </div>
  );

  export default HumanizedTable;