import * as React from 'react';
import moment from 'moment';
import classnames from 'classnames';

import { customizeMoment } from '@sqs/i18n-ui';
import { framework } from "../../cldr";
import { CLDR  } from "@phensley/cldr";
import getCldrCalendarDate from './cldrCalendarDate';
import HumanizedTable, {
  IHumanizeDataItem
} from './HumanizedTable';
import TableSectionHeader from './TableSectionHeader';

const LOCALES = ['en-US', 'es-419', 'de-DE', 'fr-FR', 'it-IT', 'pt-BR'];
const nowTimestamp = moment().valueOf();

// This object maps Moments localized tokens to the compatible CLDR.Calendar skeleton/token
const localizedTokenMap: {[key: string]: string} = {
  'LLL':'yMMMMd',
  'lllLT':'yMMMd j:mm a',
  'L hh:mm:ss':'yMd jms',
  'll':'MMMd',
  'lll': 'yMMMd',
  'llddd':'MMMEd',
  'llll':'MMMEdy',
  'LLLL':'MMMMEEEEdy',
  'LT':'jm',
  'LLLLT':'yMMMMd j:mm a',
  'LLTS':'yMd jms a'
};
// Custom moment token tokens that map 
const customTokenMap: {[key: string]: string} = {
  'MM-YYYY':'MM-y',
  'MM-DD-YYYY':'MM-dd-y',
  'MMMM Do YYYY':'MMMM d y',
  'MMMM':'MMMM',
  'MMMM YYYY':'MMMM y',
  'MMM':'MMM',
  'A':'a',
  'ddd':'E',
  'h':'h',
  'mm':'m',
  'h:mma':'h:mma',
  'ha': 'ha',
  'YYYY':'y',
  'MMM DD, YYYY hh:mm A':'MMM dd, y hh:mm a',
  'MM/YY':'MM/YY',
  'D':'d',
};

const TIME_UNITS: moment.unitOfTime.DurationConstructor[] = [
  'day',
  'month',
  'year',
  'hours',
  'minutes',
  'seconds'
];
const TIME_INTERVALS: number[] = [-366, -7, -5, -2, -1, 0, 1, 2, 5, 7, 366];

const getMomentFormattingToken = (tokenKey: any) => {
  const localeToken = moment.localeData().longDateFormat(tokenKey);
  if(tokenKey in moment.localeData().longDateFormat){
    return moment.localeData().longDateFormat(tokenKey);
  }
  return localeToken || tokenKey;
};

const renderLocalizedTable = (myMoment: moment.Moment, cldr: CLDR) => {
  const cldrDate = { 
    date: myMoment.toDate(),
    zoneId: "America/New_York"
  };
  return (
    <div className="tableContainer">
      <table>
        <tbody>
          <tr>
            <th>Match</th>
            <th>Moment Token</th>
            <th>CLDR.Calendar Token</th>
            <th>Moment Output</th>
            <th>CLDR Output</th>
          </tr>
          {Object.keys(localizedTokenMap).map((tokenKey: string)=> {

            const momentValue = myMoment.format(getMomentFormattingToken(tokenKey));
            let cldrValue = cldr.Calendars.formatDate(cldrDate, { 
                context: 'standalone', 
                skeleton: localizedTokenMap[tokenKey]
              });
              if(tokenKey === 'lllLT'){
                cldrValue = cldr.Calendars.formatDate(cldrDate, { 
                  context: 'standalone', 
                  date: 'medium',
                  time: 'short'
                });
              }
            const doValuesMatch = momentValue == cldrValue;
            return (
              <tr key={tokenKey} className={classnames({mismatch: !doValuesMatch})}>
                <td>{doValuesMatch ? <span>&#9989;</span> :  <span>&#10060;</span>}</td>
                <td>{tokenKey}</td>
                <td>{localizedTokenMap[tokenKey]}</td>
                <td>{momentValue}</td>
                <td>{cldrValue}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

const renderCustomTable = (myMoment: moment.Moment, cldr: CLDR) => {
  const cldrDate = { 
    date: myMoment.toDate(),
    zoneId: "America/New_York"
  };
  return (
    <div className="tableContainer">
      <table>
        <tbody>
        <tr>
          <th>Match</th>
          <th>Moment Token</th>
          <th>CLDR.Calendar Token</th>
          <th>Moment Output</th>
          <th>CLDR Output</th>
        </tr>
        {Object.keys(customTokenMap).map((tokenKey: string)=> {
          const momentValue = myMoment.format(getMomentFormattingToken(tokenKey));
          const cldrValue = cldr.Calendars.formatDateRaw(cldrDate, { 
              pattern: customTokenMap[tokenKey]
            });
          const doValuesMatch = momentValue == cldrValue;
          return (
            <tr key={tokenKey} className={classnames({mismatch: !doValuesMatch})}>
              <td>{doValuesMatch ? <span>&#9989;</span> :  <span>&#10060;</span>}</td>
              <td>{tokenKey}</td>
              <td>{customTokenMap[tokenKey]}</td>
              <td>{momentValue}</td>
              <td>{cldrValue}</td>
            </tr>
          );
        })}
        </tbody>
      </table>
    </div>
  );
};
const renderDateDifference = (myMoment: moment.Moment, cldr: CLDR) => {
  const output = [{
    aDate: myMoment,
    bDate: myMoment.clone().subtract(1, 'days'),
    measure: 'days',
  },
  {
    aDate: myMoment,
    bDate: myMoment.clone().subtract(1, 'weeks'),
    measure: 'weeks',
  },
  {
    aDate: myMoment,
    bDate: myMoment.clone().subtract(1, 'weeks'),
    measure: 'years',
  },
  {
    aDate: myMoment,
    bDate: myMoment.clone().add(1, 'years'),
    measure: 'years',
  },
  {
    aDate: myMoment,
    bDate: myMoment.clone().subtract(5, 'years'),
    measure: 'years',
  },

];
return (
  <div className="tableContainer">
      <table>
        <tbody>
        <tr>
          <th>aDate</th>
          <th>bDate</th>
          <th>measure</th>
          <th>cldrOutput</th>
          <th>momentOutput</th>
        </tr>
        {output.map((outputItem, i) => {

          return (
            <tr key={i}>
              <td>{outputItem.aDate.format()}</td>
              <td>{outputItem.bDate.format()}</td>
              <td>{outputItem.measure}</td>
              <td>{''}</td>
              
              <td>{
                //@ts-ignore
                outputItem.aDate.diff(outputItem.bDate, outputItem.measure)
              }</td>
            </tr>
          )
        })}
        </tbody>
      </table>
    </div>
);
}

const buildHumanizedTableData = (myMoment: moment.Moment, cldr: CLDR) => {
  const calendarData: IHumanizeDataItem[] = [];
  const relativeData: IHumanizeDataItem[] = [];

  const nowTimeStamp = myMoment.clone().valueOf();
  TIME_UNITS.forEach(unit => {
    TIME_INTERVALS.forEach(interval => {
      let xTimeStamp = myMoment.clone().add(interval, unit).valueOf();
      // Both grids need start and end date
      const dataItem = {
        startDate: moment(nowTimeStamp).format(),
        endDate: moment(xTimeStamp).format()
      };
      // Create the moment and cldr calendar/contextual date output
      calendarData.push({
        ...dataItem,
        momentOutput: moment(xTimeStamp).calendar(nowTimeStamp),
        cldrOutput: getCldrCalendarDate(xTimeStamp, nowTimeStamp, cldr)
      });
      // Create moment and cldr relative date output
      relativeData.push({
        ...dataItem,
       momentOutput: moment(xTimeStamp).from(nowTimeStamp),
       cldrOutput: cldr.Calendars.formatRelativeTime(
        cldr.Calendars.toGregorianDate({ date: nowTimeStamp }),
        cldr.Calendars.toGregorianDate({ date: xTimeStamp })
      )
      })
    });
  });
  return {
    calendarData,
    relativeData
  };
};

const DateFormattingContainer = () => {
  return ( 
    <div>
      <h2>Date Display/Formatting</h2>
        <h3>Function signatures</h3>
        <pre>
          <code>
            <span>// Momentjs</span>
            <span>moment().format(getFormattingToken(token));</span><br/>
            <span>// CLDR.Calendar localized format</span>
            <span>{`cldr.Calendars.formatDate(date, { context: 'standalone', skeleton: token});`}</span>
            <span>// CLDR.Calendar custom format</span>
            <span>{`cldr.Calendars.formatDateRaw(date, { pattern: token});`}</span>
          </code>
        </pre>
        { LOCALES.map(locale => {
          // Customize Momentjs
          customizeMoment(locale);
          const myMoment = moment(nowTimestamp).clone();
          // Get CLDR framework by locale
          const cldr = framework.get(locale);
          const {
            calendarData,
            relativeData
          } = buildHumanizedTableData(myMoment, cldr);
          return (
            <div key={locale}>
              <TableSectionHeader locale={locale} title="localized tokens" />
              {renderLocalizedTable(myMoment, cldr)}
              <TableSectionHeader locale={locale} title="relative dates" />
              <HumanizedTable tableData={relativeData} />
              <TableSectionHeader locale={locale} title="calendar dates" />
              <HumanizedTable tableData={calendarData} />
            </div>
          )}) 
        }
    </div>
  );

};

export default DateFormattingContainer;