import * as React from 'react';
import { snakeCase } from 'lodash';

const TableSectionHeader: React.FC<{locale: string, title: string}> = ({ locale, title }) => (
  <h3> 
    <a id={`${snakeCase(locale + ' ' + title)}`} href={`#${snakeCase(locale + ' ' + title)}`}>
      <span className="table-title">{title}</span> ({locale})
    </a>
  </h3>
);

export default TableSectionHeader;